/**
 * @fileoverview OpenAIHTTP
 * SSE
 */
const OpenAI = require('openai')
import {preProcess} from "@/util/config"
import store from '../../store';

/**
 * OpenAI
 * @param {string} prompt - 
 * @param {Array} history - 
 * @param {Array} files - 
 * @param {AbortController} controller - 
 * @param {Function} onopen - 
 * @param {Function} onmessage - 
 * @param {Function} onclose - 
 * @param {Function} onerror - 
 */
export async function fetchAPI({
    prompt,
    history,
    files,
    controller,
    onopen,
    onmessage,
    onclose,
    onerror
}) {
    const { setting } = store.state;
    const { model_config } = setting;
    const { pre_method } = model_config;
    const { endpoint, api_key, model_name } = setting.api_key_map['OpenAI'];

    const openai = new OpenAI({
        apiKey: api_key,
        baseURL: endpoint,
        temperature: 0,
        dangerouslyAllowBrowser: true
    });

    try {
        const stream = await openai.chat.completions.create({
            ...preProcess(model_name, prompt, history, pre_method, files, false),
            stream: true,
        });

        onopen?.();

        for await (const chunk of stream) {
            const content = chunk.choices[0]?.delta?.content || '';
            onmessage?.(content);
        }

        onclose?.();
    } catch (error) {
        onerror?.(error);
        throw error;
    }
}
